<template>
  <div
    ref="tapContainer"
    class="tap-container"
    @touchstart="handleTouchStart"
    @touchend="handleTouchEnd"
    @touchcancel="handleTouchEnd"
  >
    <div
      class="plus-one"
      v-for="plusOne in plusOnes"
      :key="plusOne.id"
      :style="plusOne.style"
    >
      +{{ tapValue }}
    </div>
    <div
      class="gradient"
      v-for="gradient in gradients"
      :key="gradient.id"
      :style="gradient.style"
    ></div>
  </div>
</template>

<script>
export default {
  name: "TapArea",
  props: {
    tapValue: {
      type: Number,
      default: 1,
    },
    remainingEnergy: {
      type: Number,
      required: true,
    },
  },
  data() {
    return {
      activeTouches: 0,
      plusOnes: [],
      gradients: [],
    };
  },
  methods: {
    handleTouchStart(event) {
      if (this.remainingEnergy <= 0) {
        return; // Не обрабатываем тап, если нет энергии
      }
      this.activeTouches += event.changedTouches.length;
      this.showPlusOne(event.changedTouches);
      this.showGradient(event.changedTouches);
      this.emitTapEvent();
      this.triggerVibration();
      this.consumeEnergy();
    },
    handleTouchEnd(event) {
      this.activeTouches -= event.changedTouches.length;
      if (this.activeTouches < 0) {
        this.activeTouches = 0;
      }
    },
    showPlusOne(touches) {
      const rect = this.$refs.tapContainer.getBoundingClientRect();
      Array.from(touches).forEach((touch) => {
        const x = touch.clientX - rect.left;
        const y = touch.clientY - rect.top;
        const id = Date.now() + Math.random(); // Уникальный идентификатор для каждого элемента
        const plusOne = {
          id,
          style: {
            position: "absolute",
            left: `${x}px`,
            top: `${y}px`,
            opacity: 1,
          },
        };
        this.plusOnes.push(plusOne);
        const plusOneElement = document.createElement("div");
        plusOneElement.textContent = `+${this.tapValue}`;
        Object.assign(plusOneElement.style, plusOne.style, {
          fontSize: "24px",
          fontWeight: "bold",
          color: "white",
          pointerEvents: "none",
          userSelect: "none",
          position: "absolute",
        });
        this.$refs.tapContainer.appendChild(plusOneElement);
        plusOneElement.animate(
          [
            { opacity: 1, transform: "translateY(0)" },
            { opacity: 0, transform: "translateY(-100px)" },
          ],
          {
            duration: 1000,
            easing: "ease-out",
            fill: "forwards",
          }
        );
        setTimeout(() => {
          this.plusOnes = this.plusOnes.filter((p) => p.id !== id);
          this.$refs.tapContainer.removeChild(plusOneElement);
        }, 1000);
      });
    },
    showGradient(touches) {
      const rect = this.$refs.tapContainer.getBoundingClientRect();
      Array.from(touches).forEach((touch) => {
        const x = touch.clientX - rect.left;
        const y = touch.clientY - rect.top;
        const id = Date.now() + Math.random(); // Уникальный идентификатор для каждого элемента
        const color = `hsl(${Math.random() * 360}, 100%, 75%)`; // Рандомный цвет
        const gradient = {
          id,
          style: {
            position: "absolute",
            left: `${x - 50}px`,
            top: `${y - 50}px`,
            width: "100px",
            height: "100px",
            background: `radial-gradient(circle, ${color} 0%, transparent 70%)`,
            opacity: 0,
          },
        };
        this.gradients.push(gradient);
        const gradientElement = document.createElement("div");
        Object.assign(gradientElement.style, gradient.style, {
          borderRadius: "50%",
          pointerEvents: "none",
          userSelect: "none",
        });
        this.$refs.tapContainer.appendChild(gradientElement);
        gradientElement.animate(
          [
            { opacity: 1, transform: "scale(0.5)" },
            { opacity: 0, transform: "scale(1.5)" },
          ],
          {
            duration: 1000,
            easing: "ease-out",
            fill: "forwards",
          }
        );
        setTimeout(() => {
          this.gradients = this.gradients.filter((g) => g.id !== id);
          this.$refs.tapContainer.removeChild(gradientElement);
        }, 1000);
      });
    },
    emitTapEvent() {
      this.$emit("tap", this.tapValue);
    },
    triggerVibration() {
      if (
        window.Telegram &&
        window.Telegram.WebApp &&
        window.Telegram.WebApp.HapticFeedback
      ) {
        window.Telegram.WebApp.HapticFeedback.impactOccurred("medium");
      } else if (navigator.vibrate) {
        navigator.vibrate(200); // Вибрация на 200 миллисекунд
      }
    },
    consumeEnergy() {
      this.$emit("consumeEnergy");
    },
  },
};
</script>

<style scoped>
.tap-container {
  position: relative;
  width: 100%;
  height: 290px;
  touch-action: none;
  border: dashed 1px #2e2e2e;
  border-radius: 16px;
  overflow: hidden;
}
</style>
